//Dependencies
import React from "react";
import { Switch, Route } from "react-router-dom";
// //Bootstrap
// import "./../node_modules/bootstrap/dist/css/bootstrap.min.css";
// import "./../node_modules/jquery/dist/jquery.js";
// import "./../node_modules/popper.js/dist/popper.min.js";
// import "./../node_modules/bootstrap/dist/js/bootstrap.min";
//CSS
import "./coronaTrackerApp.css";
//Containers
import CoronaTrackerApp from "./CoronaTrackerApp";
import CompleteReport from "./containers/CompleteReport";
import Footer from "./containers/Footer";
import Nav from "./containers/Nav";

function App() {
  return (
    <div>
      <Nav />
      <Switch>
        <Route exact path="/" component={CoronaTrackerApp} />
        {/* <Route exact path="/#/#status" component={CoronaTrackerApp} />
        <Route exact path="#/#aboutCorona" component={CoronaTrackerApp} />
        <Route exact path="#/#protection" component={CoronaTrackerApp} />
        <Route exact path="#/#faq" component={CoronaTrackerApp} /> */}
        <Route exact path="/completeReport" component={CompleteReport} />
      </Switch>
      <footer>
        <Footer />
      </footer>
    </div>
  );
}

export default App;
