import React from "react";

function StatsInIndia({ data1 }) {
  return (
    <div className="container">
      <div className="row">
        <div className="col-md-8 offset-md-2 p-2 bg-light  table-responsive">
          <table className="table table-sm table-striped">
            <thead className="thead-dark">
              <tr>
                <th>Sl No.</th>
                <th>State</th>
                <th>Confirmed</th>
                <th>Active</th>
                <th>Recovered</th>
                <th>Deaths</th>
              </tr>
            </thead>
            <tbody>
              {data1.map((data, index) => {
                return (
                  <tr key={index + 1}>
                    <th>{index + 1}</th>
                    <th>{data.provinceState}</th>
                    <th className="text-danger">{data.confirmed}</th>
                    <th className="text-danger">{data.active}</th>
                    <th className="text-success">{data.recovered}</th>
                    <th className="text-secondary">{data.deaths}</th>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      </div>
    </div>
  );
}

export default StatsInIndia;
