import React from "react";
import healthCareWorkers from "./assets/healthCareWorkers.jpg";

import AboutCorona from "./containers/AboutCorona";
import Protection from "./containers/Protection";
import FAQs from "./containers/FAQs";
import LiveStatusInIndia from "./containers/LiveStatusInIndia";

function CoronaTrackerApp() {
  return (
    <div>
      <section className="liveStatus" id="status">
        <LiveStatusInIndia />
      </section>
      <section id="aboutCorona">
        <AboutCorona />
      </section>
      <section id="protection">
        <Protection />
      </section>
      <section id="faq">
        <FAQs />
      </section>
      <img
        src={healthCareWorkers}
        alt="Health Care Workers"
        className="img-thumbnail img-fluid p-2 my-5 mx-1"
      />
    </div>
  );
}

export default CoronaTrackerApp;
